#circle-small {
  -webkit-animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation-timing-function: cubic-bezier(.6, 0, .4, 1);
  animation-delay: 0s;
  position: absolute;
  top: 15rem;
  left: -5rem;
  background: var(--color-gold);
  width: 30rem;
  height: 30rem;
  border-radius: 50%;
  opacity: 1;
  z-index: -1;
}

#circle-small img {
  width: 30rem;
  height: 30rem;
  -webkit-animation: none;
  animation: none;
  animation-timing-function: none;

}

#circle-medium {
  -webkit-animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation-timing-function: cubic-bezier(.6, 0, .4, 1);
  animation-delay: 0.3s;
  position: absolute;
  top: 0rem;
  left: -20rem;
  background: var(--color-orange);
  width: 60rem;
  height: 60rem;
  border-radius: 50%;
  opacity: 1;
  z-index: -2;
}

#circle-large {
  -webkit-animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation-timing-function: cubic-bezier(.6, 0, .4, 1);
  animation-delay: 0.6s;
  position: absolute;
  top: -15rem;
  left: -35rem;
  background: var(--color-tan);
  width: 90rem;
  height: 90rem;
  border-radius: 50%;
  opacity: 1;
  z-index: -3;
}

#circle-xlarge {
  -webkit-animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation-timing-function: cubic-bezier(.6, 0, .4, 1);
  animation-delay: 0.9s;
  position: absolute;
  top: -30rem;
  left: -50rem;
  background: var(--color-teal);
  width: 120rem;
  height: 120rem;
  border-radius: 50%;
  opacity: 1;
  z-index: -4;
}

#circle-xxlarge {
  -webkit-animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation: circle-small-scale 3s ease-in-out infinite alternate;
  animation-timing-function: cubic-bezier(.6, 0, .4, 1);
  animation-delay: 1.2s;
  position: absolute;
  top: -45rem;
  left: -65rem;
  background: var(--color-brown);
  width: 150rem;
  height: 150rem;
  border-radius: 50%;
  opacity: 1;
  z-index: -5;
}

@-webkit-keyframes circle-small-scale {
  0% {
    -webkit-transform: scale(1.0);
  }
}


@keyframes circle-small-scale {
  0% {
    transform: scale(1.0);
  }

  100% {
    transform: scale(1.1);
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
  position: relative;
}





